/** * Date Date String * (M)(d)12(h)24(H)(m)(s)(E)(q)
     1-2  * (y) 1-4 (S) 1 ( 1-3 ) * eg: * (new
    Date()).format("yyyy-MM-dd hh:mm:ss.S")==> 2006-07-02 08:09:04.423      
 * (new Date()).format("yyyy-MM-dd E HH:mm:ss") ==> 2009-03-10  20:09:04      
 * (new Date()).format("yyyy-MM-dd EE hh:mm:ss") ==> 2009-03-10  08:09:04      
 * (new Date()).format("yyyy-MM-dd EEE hh:mm:ss") ==> 2009-03-10  08:09:04      
 * (new Date()).format("yyyy-M-d h:m:s.S") ==> 2006-7-2 8:9:4.18      
 */        
Date.prototype.format=function(fmt) {         
    var o = {         
        "M+" : this.getMonth()+1, //         
        "d+" : this.getDate(), //         
        "h+" : this.getHours()%12 == 0 ? 12 : this.getHours()%12, //         
        "H+" : this.getHours(), //         
        "m+" : this.getMinutes(), //         
        "s+" : this.getSeconds(), //         
        "q+" : Math.floor((this.getMonth()+3)/3), //         
        "S" : this.getMilliseconds() //         
    };         
    var week = {         
        "0" : "",         
        "1" : "",         
        "2" : "",         
        "3" : "",         
        "4" : "",         
        "5" : "",         
        "6" : ""        
    };         
    if(/(y+)/.test(fmt)){         
        fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length));         
    }         
    if(/(E+)/.test(fmt)){         
        fmt=fmt.replace(RegExp.$1, ((RegExp.$1.length>1) ? (RegExp.$1.length>2 ? "" : "") : "")+week[this.getDay()+""]);         
    }         
    for(var k in o){         
        if(new RegExp("("+ k +")").test(fmt)){         
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));         
        }         
    }         
    return fmt;         
} 


